import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import { NavLink } from "react-router-dom";

const styles = {
    root: {
        flexGrow: 1,
    },
    grow: {
        flexGrow: 1,
    },
    menuButton: {
        marginLeft: -12,
        marginRight: 20,
    }
};

function AirReaderAppBar(props) {
    const {classes} = props;
    return (
        <div className={classes.root}>
            <AppBar position="static">
                <Toolbar>
                    {/*<IconButton className={classes.menuButton} color="inherit" aria-label="Menu">*/}
                    {/*<MenuIcon/>*/}
                    {/*</IconButton>*/}

                    <div className="flex-container">
                        {props.statusDiv}
                        <div className="flex-container selectViewBar white flex-3-1">
                            <NavLink to="/tickets" activeClassName='bold-text'>Tickets</NavLink>
                            <NavLink to="/accounts" activeClassName='bold-text'>Comptes</NavLink>
                            <NavLink to="/credit_notes" activeClassName='bold-text'>Avoirs</NavLink>
                            <NavLink to="/states" activeClassName='bold-text'>Etats</NavLink>
                            <NavLink to="/import" activeClassName='bold-text'>Import AIR</NavLink>
                            <NavLink to="/controlComponent" activeClassName='bold-text'>Control</NavLink>
                            <div style={{whiteSpace: 'pre-wrap', border: 'none'}}>{'\t / \t'}</div>
                            <NavLink to="/superDataTable" activeClassName='bold-text'>States 2.0</NavLink>
                            <NavLink to="/boComponent" activeClassName='bold-text'>B.O. generator</NavLink>

                        </div>
                        <div className="flex-1-1">
                            {props.logInDiv}
                        </div>

                        {/*<Button color="inherit">Login</Button>*/}
                    </div>
                </Toolbar>
            </AppBar>
        </div>
    );
}

AirReaderAppBar.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(AirReaderAppBar);



